<template>
  <div class="qnabox" v-if="question == num">
    <h3>{{qna.question}}</h3>
    <ul>
      <li v-for="(a,i) in qna.list" :key="i" @click="$emit('answer', i)">{{qna.list[i]}}</li>
    </ul>
  </div>
</template>

<script>
export default {
    name : 'question',
    props : {
        qna : Array,
        question : Number,
        num : Number,
    }
}
</script>

<style>
    .qnabox h3{text-align:left;font-size:28px;line-height:48px;font-weight:bold;margin:80px 0 50px;}
    .qnabox ul li{background-color:#f7b1c4;margin-bottom:10px;padding:15px;color:#fff;font-size:18px;transition:all 0.3s;cursor:pointer;}
    .qnabox ul li:hover{background-color:#f16188}
</style>