<template>
  <div class="wrap">
    <h1>심리테스트</h1>
    <div v-if="step % 2 == 1">
      <Question v-for="(a,i) in qna" :key="i" :qna="qna[i]" :num="i" :question="question" @answer="answerNumber = $event; step++"/>
    </div>
    <div v-if="step % 2 == 0">
      <Answer :qna="qna" :answerNumber="answerNumber" :question="question" @NextQuestion="question++ ; step = 1"></Answer>
    </div>
  </div>
</template>

<script>
import Question from "./views/Question.vue";
import Answer from "./views/Answer.vue";
import qna from "./assets/data.js";

export default {
  name: "App",
  data() {
    return {
      qna: qna,
      step : 1,
      question : 0,
      answerNumber : null,
    };
  },
  components: {
    Question,
    Answer,
  },
};
</script>

